.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.card-individual {
  display: flex;
  flex-direction: column;
  row-gap: 64px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: transparent;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
}
.searchbox {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 90vw;
}
.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.input-button {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 32px;
}

.sample {
  height: 35vh;
}

h3 {
  margin-left: 64px;
  margin-right: 64px;
}

.tcglogo {
  height: 75px;
  padding: 8px;
}
